.navbar_container{
    position: fixed;
    width: 100%;
    bottom: 0%;
    left: 0;
    display: flex;
    background-color: black;
    border-top: 1px solid rgb(56, 56, 56);
 
}
a{
    text-decoration: none;
    color: white;
}
.navbar_container ul {
    width: 100%;
    padding: 5px 10px;
    margin: 0;
    display: flex;
    align-items:center;
    justify-content: space-between;


}

.navbar_container a {
    text-decoration: none;
}
.navbar_container img {
    width: 35px;
    display: inline-block;
    height: 35px;
}