.new_product_container{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    padding: 5px;
    /* height: fit-content; */
    height: 90%;
    overflow: scroll;

}
.new_product_container input {
    background-color: rgb(60, 60, 60);
    width: 48%;
    border: 1px solid white;
    border-radius: 5px;
    height: 30px;
    font-size: 20px;
    margin: 5px 0px;
    color: white;
}
.compound{
    display: flex;
    flex-direction: column;
    border: 1px solid white;
    padding: 10px 5px;
    border-radius: 5px;
    overflow-y: scroll;
    height: 50%;
}
.compound_inputs{
    overflow-y: scroll;
}
.new_product_container textarea {
    background-color: black;
    border: 1px solid white;
    border-radius: 5px;
    color: white;
    font-size: 15px;
    height: 100px;
}
.new_product_container h2 {
    color: orange;
}
#value {
    width: 100px;
}
.new_product_container img {
    margin-top: 1rem;
    justify-self: center;
    align-self: center;
    width: 50px;
    height: 50px;
}
.new_product_container button {
    margin-top: 1rem;
    width: 80%;
    height: 35px;
    font-size: 20px;
    background-color: black;
    color: white;
    border: 1px solid white;
    border-radius: 5px;
    justify-self:flex-end;
    align-self: center;
}
.input_container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}